// components/history/ShipmentHistoryDialog.tsx
"use client";

import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
} from "@/components/ui/dialog";
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table";
import { ShipmentSummary } from "@/lib/history";
import { useShipmentHistory } from "@/hooks/useShipmentHistory";
import { useI18n } from "@/i18n/i18nProvider";
import { useState } from "react";

type ShipmentHistoryDialogProps = {
  open: boolean;
  onOpenChange: (open: boolean) => void;
  shipment: ShipmentSummary | null;
};

export function ShipmentHistoryDialog({
  open,
  onOpenChange,
  shipment,
}: ShipmentHistoryDialogProps) {
  const { t } = useI18n();
  const [errorKey, setErrorKey] = useState<string | null>(null);

  const { items, loading, reset } = useShipmentHistory({
    open,
    shipment,
    onError: (key) => setErrorKey(key),
  });

  const handleOpenChange = (next: boolean) => {
    if (!next) {
      reset();
      setErrorKey(null);
    }
    onOpenChange(next);
  };

  return (
    <Dialog open={open} onOpenChange={handleOpenChange}>
      <DialogContent className="max-w-lg">
        <DialogHeader>
          <DialogTitle>
            {t("historyDialog.title")}{" "}
            {shipment
              ? `– ${shipment.trackingNumber} (${shipment.companyName})`
              : ""}
          </DialogTitle>
        </DialogHeader>

        {!shipment && (
          <div className="text-sm text-slate-500">
            {t("historyDialog.noShipment")}
          </div>
        )}

        {shipment && (
          <>
            {loading && (
              <div className="text-sm text-slate-500">
                {t("historyDialog.loading")}
              </div>
            )}

            {errorKey && (
              <div className="text-sm text-red-600">{t(errorKey)}</div>
            )}

            {!loading && !errorKey && items.length === 0 && (
              <div className="text-sm text-slate-500">
                {t("historyDialog.empty")}
              </div>
            )}

            {!loading && !errorKey && items.length > 0 && (
              <div className="max-h-72 overflow-y-auto">
                <Table className="text-sm">
                  <TableHeader>
                    <TableRow>
                      <TableHead>ID</TableHead>
                      <TableHead className="text-right">
                        {t("historyDialog.price")}
                      </TableHead>
                      <TableHead className="text-right">
                        {t("historyDialog.weight")}
                      </TableHead>
                      <TableHead>{t("historyDialog.uploadedAt")}</TableHead>
                    </TableRow>
                  </TableHeader>
                  <TableBody>
                    {items.map((h) => (
                      <TableRow key={h.id}>
                        <TableCell className="font-mono text-xs">
                          {h.id}
                        </TableCell>
                        <TableCell className="text-right">
                          {Number(h.price).toFixed(2)} Kč
                        </TableCell>
                        <TableCell className="text-right">{h.weight}</TableCell>
                        <TableCell>
                          {new Date(h.uploadedAt).toLocaleString("cs-CZ")}
                        </TableCell>
                      </TableRow>
                    ))}
                  </TableBody>
                </Table>
              </div>
            )}
          </>
        )}
      </DialogContent>
    </Dialog>
  );
}
